import React from 'react'
import './App.css'
import Footer from './src/Footer'
import History from './src/History'
import TicketStatus from './src/TicketStatus'
import Distraction from './src/Distraction';

export default () => {
	return (
		<div className='container'>
			<TicketStatus />
			<div className='history-wrapper'>
				<History />
				<Distraction show={false} videoId='PL5YKg2JSGlSy9_l-EC5B7kuj8DqgKuYMe' />
			</div>
			<Footer />
		</div>
	)
}
