import React from 'react'

export default ({ show, videoId }) => {
	const display = show ? 'block' : 'none'
	return (
		<div className='distraction' style={{ display }}>
			{show ?
                <iframe
                    title='Unique title'
                    frameBorder='0'
                    src={`https://www.youtube.com/embed/videoseries?list=${videoId}&autoplay=1&loop=1`}
                    allow='autoplay; encrypted-media'
			/> : null}
		</div>
	)
}
