import React, { Component } from 'react'

export default class History extends Component {
	state = {
		historyList: [
			{ ticket: 11, window: 2, time: '09:16' },
			{ ticket: 10, window: 2, time: '09:10' },
			{ ticket: 9, window: 2, time: '09:03' }
		]
	}

	mapHistory = (history, value) => (
		<tr key={value}>
			<td>{history.ticket}</td>
			<td>{history.window}</td>
			<td>{history.time}</td>
		</tr>
	)
	render() {
		const { historyList } = this.state
		const history = historyList.length ? historyList.map(this.mapHistory)
			: <tr><td>###</td><td>##</td><td>##:##</td></tr>
		return (
			<div className='history'>
				<div className='history-title'>Senhas chamadas</div>
				<table>
					<tbody>
						<tr>
							<th>Senha</th>
							<th>Guichê</th>
							<th>Horário</th>
						</tr>
						{history}
					</tbody>
				</table>
			</div>
		)
	}
}
