import React, { Component } from 'react'

export default class TicketStatus extends Component {
	state = { ticket: 12, window: 2 }
	render() {
		const { ticket, window } = this.state
		return (
			<div className='current-ticket-status'>
				<div className='ticket-number-wrapper'>
					<span className='ticket-label'>Senha</span>
					<span className='ticket'>{ticket}</span>
				</div>
				<div className='current-window-wrapper'>
					<span className='window-label'>Guichê</span>
					<span className='window'>{window}</span>
				</div>
			</div>
		)
	}
}
