import React, { Component } from 'react'

export default class Footer extends Component {
	state = {
		newsList: [
			'Lorem ipsum dolor sit amet consectetur adipisicing elit. At, maxime?',
			'Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident beatae doloremque officia.',
			'Lorem ipsum dolor sit amet consectetur adipisicing	elit. Cupiditate, numquam rem.',
			'Lorem ipsum dolor sit amet consectetur adipisicing	elit. Enim!'
		],
		defaultNews:
			'Lorem ipsum dolor sit amet consectetur adipisicing	elit. Cupiditate, numquam rem.',
		time: '12:16',
		date: '15/06'
	}

	mapNews = (news, index) => <li key={index} className='news-item'>{news}</li>
	render() {
		const { newsList, defaultNews, time, date } = this.state

		const news = newsList.length
			? newsList.map(this.mapNews)
			: <li className='news-item'>{defaultNews}</li>

		return (
			<div className='time-and-news-wrapper'>
				<div className='time-and-data footer-item'>
					<span className='time'>{time}</span>
					<span className='date'>{date}</span>
				</div>
				<div className='news-wrapper'>
					<ul className='footer-item'>{news}</ul>
				</div>
			</div>
		)
	}
}
